import { newSpecPage } from '@stencil/core/testing';
import { PrestoActionBarUi } from '../presto-action-bar-ui';

describe('presto-action-bar', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoActionBarUi],
      html: `<presto-action-bar-ui></presto-action-bar-ui>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-action-bar-ui>
        <mock:shadow-root>
          <div class="bar">
            <span class="bar__content" part="content">
              <slot></slot>
            </span>
            <span class="bar__button" part="button">
              <slot name="button"></slot>
            </span>
          </div>
        </mock:shadow-root>
      </presto-action-bar-ui>
    `);
  });
});
